import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const singleRoutes = [
  {
    path: '/product/show_design/:id',
    name: '',
    // component: () => import('../pages/product/show_design'),
    component: () => import('../pages/design/play/play.vue'),
    meta: { nav: 'product' }
  }
]


export default new Router({
    //mode: 'history',
    routes: [
        ...singleRoutes,
        {
            path: '/login',
            name: '',
            component: () => import('../pages/ucenter/login'),
        },
        {
            path: '/register',
            name: '',
            component: () => import('../pages/ucenter/register'),
        },
        {
            path: '/gallery/items/show',
            component: () => import('../components/layout'),
            children: [
                {path: ':id', name: '', component: () => import('../pages/gallery/GalleryShow'), meta: { nav: 'gallery' }}
            ]
        },
        {
            path: '/gallery/edit',
            component: () => import('../components/layout'),
            children: [
                {path: ':id', name: '', component: () => import('../pages/gallery/EditGallery'), meta: { nav: 'gallery' }},
            ]
        },
        // {
        //     path: '/product/detail/:productId/design',
        //     name: '',
        //     component: () => import('../pages/product/ProductDesign'),
        //     meta: { nav: 'product' }
        // },
        {
            path: '/design',
            component: () => import('../components/layout/alive'),
            children: [
                {path: ':productId/playground', name: '', component: () => import('../pages/design/product/product.vue'), meta: { nav: 'product' }},
                {
                    path: 'image', component: () => import('../pages'), meta: { nav: 'image' },
                    children: [
                        {
                            path: 'list', component: () => import('../pages/Layout'), meta: { nav: 'image' },
                            children: [
                                {path: ':status', name: '', component: () => import('../pages/image/ImageList'), meta: { nav: 'image' },}
                            ]
                        }
                    ]
                },
            ]
        },
        {
            path: '/',
            name: '',
            redirect: '/home',
            component: () => import('../pages'),
            children: [
                {
                    path: 'home', name: '', component: () => import('../pages/home'), meta: { title: '', nav: 'home' },
                },
                {
                    path: '/gallery',
                    name: '',
                    redirect: '/gallery/items',
                    component: () => import('../pages/Layout'),
                    children: [
                        { path: 'items', name: '', component: () => import('../pages/album/list'), meta: { nav: 'gallery' } },
                        { path: 'items/:id', name: '', component: () => import('../pages/album/pics'), meta: { nav: 'gallery' } },
                        // { path: 'items', name: '', component: () => import('../pages/gallery/GalleryList'), meta: { nav: 'gallery' } },
                        // { path: 'items/:id', name: '', component: () => import('../pages/gallery/GalleryDetail'), meta: { nav: 'gallery' } },
                    ]
                },
                {
                    path: '/product',
                    name: '',
                    redirect: '/product/line',
                    component: () => import('../pages/Layout'),
                    children: [
                        {
                            path: 'line', name: '', component: () => import('../pages/product/ProductLine'), meta: { nav: 'product' },
                            children: [
                                { path: ':lineId?', name: '', component: () => import('../pages/product/ProductList'), meta: { nav: 'product' } },
                            ]
                        },
                        { path: 'detail/:productId', name: '', component: () => import('../pages/product/ProductDetail'), meta: { nav: 'product' } },
                    ]
                },
                {
                    path: '/video',
                    name: '',
                    redirect: '/video/index',
                    component: () => import('../pages/Layout'),
                    children: [
                        {
                            path: 'index', name: '', component: () => import('../pages/video/index'), meta: { nav: 'video' }
                        }
                    ]
                },
                // {
                //     path: '/product/design',
                //     component: () => import('../pages/product/ProductDesign'),
                //     children: [
                //         {path: ':productId', name: '', component: () => import('../components/product/design/playground'), meta: { nav: 'product' }},
                //     ],
                // },
                {
                    path: '/360',
                    name: '360',
                    redirect: '/360/list',
                    component: () => import('../pages/Layout'),
                    children: [
                       { path: 'list', name: '360', component: () => import('../pages/360/360Main'), meta: { nav: 'show360' },
                          // children: [
                          //   {path: 'show/:id', name: '360', component: () => import('../pages/360/360Detail'), meta: {nav: 'show360'}}
                          // ]
                       },
                        // { path: 'list/:id', name: '360', component: () => import('../pages/360/360Detail'), meta: { nav: 'show360' } },
                    ]
                },
                {
                    path: '/image',
                    name: '',
                    redirect: '/image/list',
                    component: () => import('../pages/Layout'),
                    children: [
                        { path: 'list/:status?', component: () => import('../pages/image/ImageList'), meta: { nav: 'image' },
                          children: [
                            { path: 'items', name: '', component: () => import('../pages/image/list'), meta: { nav: 'image' } },
                            // { path: 'background', name: '', component: () => import('../pages/image/Background'), meta: { nav: 'image' } },
                            { path: 'background', name: '', component: () => import('../pages/design/background/background.vue'), meta: { nav: 'image' } },
                            { path: 'product', name: '', component: () => import('../pages/image/ProductDetail'), meta: { nav: 'image' } },
                            { path: '360/:id', name: '360', component: () => import('../pages/image/360Detail'), meta: { nav: 'image' } },
                          ] },
                        // { path: 'list/:status?', name: '', component: () => import('../pages/image/ImageList'), meta: { nav: 'image' } },
                        // { path: 'background', name: '', component: () => import('../pages/image/Background'), meta: { nav: 'image' } },
                        // { path: 'product', name: '', component: () => import('../pages/image/ProductDetail'), meta: { nav: 'image' } },
                        // { path: '360/:id', name: '360', component: () => import('../pages/image/360Detail'), meta: { nav: 'image' } },
                    ]
                },
                {
                    path: '/publish',
                    name: '',
                    redirect: '/publish/do',
                    component: () => import('../pages/Layout'),
                    children: [
                        { path: 'do', name: '', component: () => import('../pages/publish/Publish'), meta: { nav: 'publish' } },
                    ]
                },
                {
                    path: '/order',
                    name: '',
                    redirect: '/order/list',
                    component: () => import('../pages/Layout'),
                    children: [
                        { path: 'list', name: '', component: () => import('../pages/order/OrderList'), meta: { nav: 'order' } },
                    ]
                },
                {
                    path: '/ucenter',
                    name: '',
                    redirect: '/ucenter/home',
                    component: () => import('../pages/Layout'),
                    children: [
                        { path: 'home', name: '', component: () => import('../pages/ucenter'), meta: { nav: 'ucenter' } },
                    ]
                },
                {
                  path: '/apply',
                  name: '',
                  redirect: '/apply/home',
                  component: () => import('../pages/Layout'),
                  children: [
                      { path: 'home', name: '', component: () => import('../pages/apply'), meta: { nav: 'apply' } },
                  ]
                }
            ]
        },
        // {
        //     path: 'ucenter',
        //     component: () => import('../views/mizu'),
        //     children: [
        //         {
        //             path: 'all',
        //             name: 'mizu-fang-all',
        //             component: () => import('../views/mizu/items')
        //         }
        //     ]
        // }
    ]
})



